import React from 'react'
import { Link } from 'react-router-dom'
import MenuPB from './MenuPB.js'
import TranslatedComponent from '../../utils/TranslatedComponent.js';
// The Header creates links that can be used to navigate
// between routes.

class Menu extends React.Component {
	constructor(props) {
        super(props);
        this.state = {
        	'toogle':true,
        	'show': 'hideMenu'
        };
        this.clickHandler = this.clickHandler.bind(this);
    }
	clickHandler(){
		this.setState({
			'toogle': !this.state.toogle,
		    'show': this.state.toogle ? 'showMenu' : ''
		 });
    }
  	render() {
	  	return(
	  		<div onClick={ this.clickHandler } >
		  		<MenuPB ></ MenuPB>
		  		<menuOptions 
		  			className={
						this.state.show 
	      			} 
	      		>			    
				    <nav>
				    	<div className="scrollCont" >
				    		<div className="scrollableCont" >
						        <Link to='/home'><div>{this.translate('menu.home')}</div></Link>
						        <Link to='/content'><div>{this.translate('menu.content')}</div></Link>
						        <Link to='/podcast'><div>{this.translate('menu.podcast')}</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>FIN</div></Link>
						    </div>
					    </div>
				    </nav>
				</menuOptions>
				
			</div>
			
	  	)
	  }

}
Menu.propTypes = {
  //who: React.PropTypes.string.isRequired,
};


// Returns nothing because it mutates the class
TranslatedComponent(Menu);
export default Menu


