import React from 'react'
import TranslatedComponent from '../../utils/TranslatedComponent.js';
import { Link } from 'react-router-dom'


class Auth extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
     'user':'',
     'pwd':''
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    console.log(event.target.id);
    this.setState({[event.target.id]:event.target.value});
  }

  handleSubmit(event) {
    console.log(this.state);
    event.preventDefault();
  }
  componentDidMount() {
    // Will execute as normal
  }

  render() {
    return (
      <auth>

        <div className="basicOuter" >
      	  	<div className="basicInner">
            
      	    	<h1>{this.translate('login')}</h1>
              <form onSubmit={this.handleSubmit}>
                <div><label>User</label></div>
                <div><input id="user" type="text"  onChange={this.handleChange} /></div>
                <div><label>Password</label></div>
                <div><input id="pwd" type="text" onChange={this.handleChange} /></div>
                <div><input type="submit" value="Submit" /></div>
                <div className="submitPB" onClick={this.handleSubmit}>SUBMIT </div>
              </form>
              <Link to='/home'><div className="backPB" >{this.translate('initSession')}</div></Link>
      		</div>
    	  </div>
      </auth>  
    );
  }
}

Auth.propTypes = {
  //who: React.PropTypes.string.isRequired,
};


// Returns nothing because it mutates the class
TranslatedComponent(Auth);
export default Auth;


