import React from 'react'
import { Link } from 'react-router-dom'
import SettingsPB from './SettingsPB.js'
import TranslationPicker from '../TranslationPicker.js';
import TranslatedComponent from '../../utils/TranslatedComponent.js';
// The Header creates links that can be used to navigate
// between routes.

class Settings extends React.Component {
	constructor(props) {
        super(props);
        this.state = {
        	'toogle':true,
        	'show': 'hideMenu'
        };
        this.clickHandler = this.clickHandler.bind(this);
    }
	clickHandler(){
		this.setState({
			'toogle': !this.state.toogle,
		    'show': this.state.toogle ? 'showMenu' : ''
		 });
    }
  	render() {
	  	return(
	  		<div onClick={ this.clickHandler } >
		  		<SettingsPB ></ SettingsPB>
		  		<menuOptions 
		  			className={
						this.state.show 
	      			} 
	      		>
	      			<nav>
	      				<div className="scrollCont" >
				    		<div className="scrollableCont" >
				    			<TranslationPicker />
				        		<Link to='/user'><div>{this.translate('menu.user')}</div></Link>
				        		<Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>...</div></Link>
						        <Link to='/notdefined'><div>FIN</div></Link>
				        	</div>
				        </div>
				    </nav>			    

				</menuOptions>
				
			</div>
			
	  	)
	  }

}
Settings.propTypes = {
  //who: React.PropTypes.string.isRequired,
};


// Returns nothing because it mutates the class
TranslatedComponent(Settings);
export default Settings


